<template>
  <div>
    <svg :width="width" :height="height" class="monogram">
      <defs>
        <linearGradient id="grad1" x1="0%" y1="0%" x2="0%" y2="100%">
          <stop
            offset="0%"
            style="stop-color: rgb(144, 247, 152); stop-opacity: 1"
          />
          <stop
            offset="100%"
            style="stop-color: rgb(33, 175, 93); stop-opacity: 1"
          />
        </linearGradient>
      </defs>
      <polygon :points="monogramPoints" fill="url(#grad1)" class="shadow-lg" />
      <text
        id="rankLabel"
        class="text"
        x="50%"
        y="30%"
        alignment-baseline="middle"
        text-anchor="middle"
      >
        Rank
      </text>
      <text
        id="rank"
        class="text"
        x="50%"
        y="62%"
        alignment-baseline="middle"
        text-anchor="middle"
      >
        {{ letters }}
      </text>
    </svg>
  </div>
</template>
<script>
export default {
  name: "Monogram",
  props: {
    width: {
      type: [Array, Number, String, Function],
      default: 200,
    },
    letters: {
      type: [Array, Number, String, Function],
      default: "No Rank",
    },
    primaryColor: {
      type: [Array, Number, String, Function],
      default: "blue",
    },
  },
  data() {
    return {
      fillColor: {
        fill: this.primaryColor,
      },
    };
  },
  computed: {
    monogramPoints: function () {
      return this.calculatePointsForHexagon();
    },
    height: function () {
      return parseFloat(((this.width / Math.sqrt(3)) * 2).toFixed(2));
    },
  },
  methods: {
    calculatePointsForHexagon() {
      var points = [
        [parseFloat((this.width / 2).toFixed(2)), 0],
        [
          parseFloat(this.width),
          parseFloat((this.width / Math.sqrt(3) / 2).toFixed(2)),
        ],
        [
          parseFloat(this.width),
          parseFloat(
            (this.width / Math.sqrt(3) + this.width / Math.sqrt(3) / 2).toFixed(
              2
            )
          ),
        ],
        [
          parseFloat((this.width / 2).toFixed(2)),
          parseFloat(((this.width / Math.sqrt(3)) * 2).toFixed(2)),
        ],
        [
          0,
          parseFloat(
            (this.width / Math.sqrt(3) + this.width / Math.sqrt(3) / 2).toFixed(
              2
            )
          ),
        ],
        [0, parseFloat((this.width / Math.sqrt(3) / 2).toFixed(2))],
      ];
      return points.join(" ");
    },
  },
};
</script>
<style lang="scss" scoped>
// Feel free to remove the scoped scss. Acts as a starting point
$black: hsl(0, 0%, 4%) !default;
$weight-bold: 700 !default;
$family-serif: Constantia, "Lucida Bright", "Lucidabright", "Lucida Serif",
  "Lucida", "DejaVu Serif", "Bitstream Vera Serif", "Liberation Serif", Georgia,
  serif;
$text-2xl: 1.5rem;
.monogram {
  #rank {
    fill: white;
    font-family: $family-serif;
    font-weight: $weight-bold;
    font-size: 25px;
  }
  #rankLabel {
    fill: white;
    font-family: $family-serif;
    font-weight: $weight-bold;
    font-size: 12px;
  }
}

</style>