<template>
  <div ref="content">
    <div class="mobile-screen">
      <div id="my_dataviz"></div>
    </div>
  </div>
</template>

<script>
import * as d3 from "d3";
import "d3-scale-chromatic";
import i18n from "@/plugins/i18n";
export default {
  name: "ImprovementExpenseComp",
  components: {},
  props: {
    dataset: {
      type: [Array, Object, String, Function],
      default: null,
    },
    colors: {
      type: [Array, Object, String],
      default: null,
    },
    labels: {
      type: [Array, Object, String],
      default: null,
    },
    dropDownList: {
      type: [Array, Object, String],
      default: null,
    },
    optionlist: {
      type: [Array, Object, String],
      default: null,
    },
    expenseType: {
      type: String,
      default: "expense",
    },
    secondGraph: {
      type: Boolean,
      default: false,
    },
    totalMonths: {
      type: [String, Number, Object],
      default: 0,
    },
    startYear: {
      type: [Array, Object, String, Number],

      default: null,
    },
    startMonth: {
      type: [Array, Object, String, Number],
      default: null,
    },
    xLabelsProp: {
      type: [Array, Object, String, Number],
      default: null,
    },
    xMinMaxProp: {
      type: [Array, Object, String, Number],
      default: null,
    },
  },
  data() {
    return {
      svg: null,
      getData: [],
      height: null,
      width: null,
      currentOpacity: null,
      selectedOption: "",
      allSelectOptions: [],
      allGroupOptions: [],
      ellipseColor: ["white", "gray"],
    };
  },
  computed: {
    getScreen() {
      return window.screen.width;
    },
    isMobileScreen() {
      if (
        navigator.userAgent.match(/Android/i) ||
        navigator.userAgent.match(/webOS/i) ||
        navigator.userAgent.match(/iPhone/i) ||
        navigator.userAgent.match(/iPad/i) ||
        navigator.userAgent.match(/iPod/i) ||
        navigator.userAgent.match(/BlackBerry/i) ||
        navigator.userAgent.match(/Windows Phone/i)
      ) {
        return true;
      } else {
        return false;
      }
    },
    getallGroupOptions() {
      return this.allSelectOptions;
    },
  },
  created() {
    i18n.locale = "ja";
    localStorage.setItem("locale", "ja");
  },
  async mounted() {
    this.getData = this.dataset;
    console.log("this.getData", this.totalMonths);
    if (this.getData.length > 0) {
      await this.draw();
    }
  },
  methods: {
    /**Function to draw based on data and display in graph format */
    draw() {
      /** set the dimensions and margins of the graph*/
      var margin = { top: 10, right: 70, bottom: 30, left: 200 };
      // let setWidth = '100%'
      (this.width = 950 - margin.left - margin.right),
        (this.height = 450 - margin.top - margin.bottom);
      d3.select("svg #my_dataviz").remove();
      // let setScreenWidth = this.getScreen > 500 ? "100%" : "600";
      /** append the svg object to the body of the page */
      this.svg = d3
        .select("#my_dataviz")
        .append("svg")
        .attr("viewBox", "0 0 1000 510")
        .attr("width", "100%")
        .append("g")
        .attr("transform", "translate(" + 0 + "," + margin.top + ")");
      let xx = this.labels.length;
      let barSize = this.height / (xx + 1);
      let i, mmm;
      mmm = [];
      for (i = 0; i < xx; i++) {
        mmm.push(i * 15);
      }
      var bar = this.svg
        .selectAll("g")
        .data(mmm)
        .enter()
        .append("g")
        .attr("transform", function (d, i) {
          return "translate(" + 0 + "," + i * barSize + ")";
        });
      let longTermData = {
        data: [
          { x: 100, y: 0 },
          { x: 90, y: 0 },
          { x: 68, y: 0 },
          { x: 90, y: 0 },
          { x: 68, y: 0 },
          { x: 90, y: 0 },
          { x: 68, y: 0 },
          { x: 90, y: 0 },
          { x: 68, y: 0 },
          { x: 90, y: 0 },
          { x: 68, y: 0 },
          { x: 90, y: 0 },
        ],
        width: 70,
      };
      let midTermData = {
        data: [
          { x: 150, y: 0 },
          { x: 150, y: 0 },
          { x: 150, y: 0 },
          { x: 150, y: 0 },
          { x: 150, y: 0 },
          { x: 150, y: 0 },
        ],
        width: 150,
      };
      let shortTermData = {
        data: [
          { x: 200, y: 0 },
          { x: 380, y: 0 },
        ],
        width: 380,
      };
      let self=this;
      this.svg
        // .append("g")
        .selectAll("rect")
        .data(function () {
          if (self.totalMonths == 3) {
            return shortTermData.data;
          }
          if (self.totalMonths == 6) {
            return midTermData.data;
          }
          if (self.totalMonths == 12) {
            return longTermData.data;
          }
          return midTermData.data;
        })
        .enter()
        .append("rect")
        .attr("transform", "translate(150," + 0 + ")")
        .attr("x", (d, i) => {
          return d.x * i;
        })
        .attr("y", 0)
        .attr("width", function () {
          if (self.totalMonths == 3) {
            return shortTermData.width;
          }
          if (self.totalMonths == 6) {
            return midTermData.width;
          }
          if (self.totalMonths == 12) {
            return longTermData.width;
          }
          return midTermData.width;
        })
        .attr("height", 350)
        .attr("stroke", "white")
        .attr("fill", (d, i) => {
          console.log(d, "d", i, "i");
          if (i % 2 == 0) return "#F0F5FA";
          return "#FFFFFF";
        })
        .attr("style", "opacity:0.5");
      bar
        .append("line")
        .attr("x1", 0)
        .attr("y1", 0)
        .attr("x2", 0)
        .attr("y2", barSize)
        .attr("stroke", function () {
          return "white";
        })
        .attr("style", "stroke-width:3");
      bar
        .append("line")
        .attr("x1", "10%")
        .attr("y1", barSize)
        .attr("x2", "90%")
        .attr("y2", barSize)
        .attr("style", function (d, i) {
          if (i == 5) {
            return "stroke:#000;stroke-width:1;";
          }
          return "";
        });
      bar
        .append("line")
        .attr("x1", "90%")
        .attr("y1", 0)
        .attr("x2", "10%")
        .attr("y2", 0)
        .attr("style", "stroke:#222;stroke-width:0.5;");

      bar
        .select("foreignObject")
        .data([
          "ランクS",
          "ランクA",
          "ランクB",
          "ランクC",
          "ランクD",
          "ランクE",
        ])
        .enter()
        .append("foreignObject")
        .attr("transform", function (d, i) {
          return "translate(" + 0 + "," + i * barSize + ")";
        })
        // .attr("transform", function (d, i) {
        //   return "rotate(90)";
        // })
        .attr("x", 2)
        .attr("y", barSize - 42)
        .attr("width", "10%")
        .attr("height", 50)
        .attr("stroke", "black")
        .attr("text-anchor", "middle")
        .append("xhtml:body")
        .html((d) => {
          if (this.secondGraph) {
            let x = d.split(" ");
            return `<div class='text-center'>${
              x[0]
            }</div><div class='text-center'>${x[1] ? x[1] : ""}</div>`;
          }
          return d;
        })
        .attr(
          "style",
          "color:#033170;text-align:left;word-break:break-word;background:transparent;font-size:10px;font-weight:600"
        );
      let xLabels = [];
      let xLablesMinMax = [];
      let startMonth = this.startMonth,
        startYear = this.startYear;
      for (let index = 0; index < this.totalMonths; index++) {
        let x = startYear + this.$t("YEAR") + startMonth + this.$t("MONTH");
        xLabels.push(x);
        let xMinMax =
          String(startYear) +
          (startMonth > 9 ? String(startMonth) : `0${startMonth}`);
        xLablesMinMax.push(parseInt(xMinMax));
        if (startMonth >= 12) {
          startYear = startYear + 1;
          startMonth = 1;
        } else {
          if (startMonth >= 1 && startMonth < 12) {
            startMonth = startMonth + 1;
          }
        }
      }
      /** Call the method */
      var dataReady = this.getData.map((grpName) => {
        return {
          name: grpName.name,
          values: this._.sortBy(
            grpName.judgementPerformance.map(function (d) {
              return {
                time: xLablesMinMax.indexOf(d.judg_monthly),
                rank: parseInt(d.judg_company),
                value: 6.2 - (parseInt(d.judg_company) - 0.4),
                gname: grpName.name,
              };
            }),
            "time"
          ),
        };
      });
      if (xLablesMinMax.length == parseInt(this.totalMonths)) {
        this.update(
          dataReady,
          xLablesMinMax,
          xLabels,
          this.svg,
          this.height,
          this.width
        );
      }
    },
    update(dataReady, xLablesMinMax) {
      // radial gradient

      /** Add X axis --> it is a date format */
      var x = d3
        .scaleLinear()
        .domain([0, xLablesMinMax.length - 1])
        .range([50, this.width + 120]);
      var xAxis = d3
        .axisBottom(x)
        .ticks(this.totalMonths - 1)
        .tickFormat((d, i) => {
          let year = String(xLablesMinMax[i]).slice(0, 4);
          let month = String(xLablesMinMax[i]).slice(4, 6);
          return `${year}年${month}月`;
        });

      var ticksList = this.svg
        .append("g")
        .attr("transform", "translate(100," + (this.height - 60) + ")")
        .call(xAxis)
        .call((g) => g.selectAll(".domain").attr("style", "display:none"))
        .call((g) => g.selectAll(".tick line").attr("style", "display:none"))
        .call((g) =>
          g
            .selectAll(".tick text")

            .attr("style", "font-weight:700;color:#033170")
            .attr("dy", () => {
              return this.isMobileScreen ? 5 : 14;
            })
            .attr("dx", () => (this.isMobileScreen ? -40 : 10))
            .attr("transform", () => {
              return this.isMobileScreen ? "rotate(-55)" : "rotate(0)";
            })
        );

      var ticks = ticksList.selectAll(".tick");
      ticks.each(function () {
        d3.select(this)
          .append("circle")
          .attr("r", 4)
          .attr("fill", "gray")
          .attr("stroke", "gray");
      });
      /** Add Y axis */
      var y = d3
        .scaleLinear()
        .domain([0, 6])
        .range([this.height - 50, 0]);
      /** Add the lines */
      var line = d3
        .line()
        .x(function (d) {
          return x(+d.time) + 100;
        })
        .y(function (d) {
          return y(+d.value);
        });
      this.svg
        .selectAll("myLines")
        .data(dataReady)
        .enter()
        .append("path")
        .attr("class", function (d) {
          return d.name;
        })
        .attr("d", function (d) {
          if (d.name == "同規模平均") {
            return line(d.values);
          }
          // return line(d.values);
        })
        .attr("stroke", (d) => {
          let color = d.name == "同規模平均" ? "#BCCADD" : "#003170";
          return color;
        })
        .style("stroke-width", (d) => {
          let width = d.name == "同規模平均" ? 2 : 3;
          return width;
        })
        .style("fill", "none");
      /** Add the points */
      var nodes = this.svg
        /** First we need to enter in a group */
        .selectAll("myDots")
        .data(dataReady)
        .enter()
        .append("g")
        .style("fill", (d, i) => {
          if (this.secondGraph == true) {
            return this.ellipseColor[i];
          } else {
            return "white";
          }
        });
      /** Second we need to enter in the 'values' part of this group */
      nodes
        .selectAll("circle")
        .data(function (d) {
          return d.values.filter(
            (item) =>
              !isNaN(parseInt(item.value)) && !isNaN(parseInt(item.time))
          );
        })
        .enter()
        .append("circle")
        .attr("class", "dot")
        .attr("cx", function (d) {
          return x(d.time) + 100;
        })
        .attr("cy", function (d) {
          return y(d.value);
        })
        .attr("r", (d) => {
          let radius = d.name == "同規模平均" ? 5 : 7;
          return radius;
        })
        // .attr("stroke", "gray")
        .attr("fill", (d) => {
          console.log(d.gname);
          let color = d.gname == "同規模平均" ? "#BCCADD" : "#003170";
          return color;
        });

      nodes
        .selectAll("foreignObject")
        .data(function (d) {
          return d.values.filter(
            (item) =>
              !isNaN(parseInt(item.value)) && !isNaN(parseInt(item.time))
          );
        })
        .enter()
        .append("foreignObject")
        .attr("x", function (d) {
          return x(d.time) + 72.5;
        })
        .attr("y", function (d) {
          // if (d.value > 4.5) {
          //   return y(d.value) + 20;
          // }
          return y(d.value) - 28.3;
        })
        .attr("width", 80)
        .attr("height", 90)
        .attr("stroke", "black")
        .attr("text-anchor", "middle")
        .attr("style", "color:white")
        .append("xhtml:body")

        .html(function (d) {
          let rankLtters = [
            {
              rank: "S",
              color1: "#FF7D66",
              color2: "#FF671F",
              color3: "#FF7D66",
            },
            {
              rank: "A",
              color1: "#A2DBFF",
              color2: "#4068EDE6",
              color3: "#A2DBFF",
            },
            {
              rank: "B",
              color1: "#C4EAFF",
              color2: "#93B5C4 ",
              color3: "#C4EAFF",
            },
            {
              rank: "C",
              color1: "#FFF040",
              color2: "#FA9E22",
              color3: "#FFF040",
            },
            {
              rank: "D",
              color1: "#85F780",
              color2: "#0AF932",
              color3: "#85F780",
            },
            {
              rank: "E",
              color1: "#EDBEDF",
              color2: "#F90AF5",
              color3: "#EDBEDF",
            },
          ];
          let yName;
          if (d.rank) {
            yName = rankLtters[d.rank - 1];
          }
          // if (d.value <= 1) {
          //   yName = rankLtters[4];
          // } else if (d.value <= 2) {
          //   yName = rankLtters[3];
          // } else if (d.value <= 3) {
          //   yName = rankLtters[2];
          // } else if (d.value <= 4) {
          //   yName = rankLtters[1];
          // } else if (d.value <= 5) {
          //   yName = rankLtters[0];
          // } else {
          //   yName = rankLtters[5];
          // }

          return `<div style="color:white;text-align:center;padding-top:10px;background: transparent linear-gradient(180deg, ${yName.color1} 0%, ${yName.color2} 100%) 0% 0% no-repeat padding-box;border-radius:50%;height:55px;width:55px;box-shadow: 0px 3px 10px ${yName.color3};"><div style="font-size:12px">Rank</div>
                <div class="font-weight-bold" style="font-size:20px">${yName.rank}</div></div>`;
        })
        .attr("style", (d) => {
          if (d.gname == "同規模平均") {
            return "color:white;text-align:center;font-size:13px;background:transparent;";
          } else {
            return "text-align:center;font-size:14px;height:55px;width:55px;border-radius:50%;";
          }
        });
    },
    yealyExpense() {
      this.$router.push({ name: "improvement_expense" });
    },
    monthlyExpense() {
      this.$router.push({ name: "improvement_expense_judgement" });
    },
    onExpenseClick() {
      this.$emit("expense-change", "");
    },
  },
};
</script>

<style scoped lang="scss">
.color-orange {
  background-color: #ff4e00;
  background-image: linear-gradient(315deg, #f3682c 0%, #ec9f05 74%);
}

.btn-custom-color {
  background-color: #062854;
  border-color: #062854;
  color: white;
}
.mobile-screen {
  overflow-x: auto;
}
</style>