<template>
  <div ref="content">
    <div class="judgCompMobile">
      <b-row class="d-none d-lg-flex justify-content-end">
        <b-col lg="4" md="4" sm="12" class="text-right">
          <b-button
            @click="getThreeMonthsDetails"
            :class="{ mntbtn: mntBtnFlag, mnt2btn: !mntBtnFlag }"
            :disabled="!getRank(clientJudgInfoTObject.judg_company)"
            class="mntborder"
            >3ケ月</b-button
          >
          <b-button
            @click="getSixMonthsDetails"
            :class="{ mntbtn: mntBtnFlag2, mnt2btn: !mntBtnFlag2 }"
            :disabled="!getRank(clientJudgInfoTObject.judg_company)"
            class="mnt2border"
            >6ケ月</b-button
          >
          <b-button
            @click="getAllMonthsDetails"
            :disabled="!getRank(clientJudgInfoTObject.judg_company)"
            :class="{ mntbtn: mntBtnFlag3, mnt2btn: !mntBtnFlag3 }"
            class="border-radius-imprv"
            >12ケ月</b-button
          >
        </b-col>
      </b-row>
    </div>
    <b-row class="d-flex justify-content-center">
      <b-col
        lg="6"
        xl="6"
        md="12"
        sm="12"
        class="font-weight-bold text-center mb-3 mb-md-4"
      >
        <div class="rankIcon">
          <div v-if="getRank(clientJudgInfoTObject.judg_company)" class="mb-3">
            <monogram
              :letters="getRank(clientJudgInfoTObject.judg_company)"
              width="62"
            />
          </div>

          <template v-if="expenseType == 'judgement' && clientJudgInfoTObject">
            <div
              class="font-weight-bold text-center"
              style="
                border: 1px solid blue;
                border-radius: 16px;
                padding: 3px;
                display: inline-block;
                width: 200px;
                color: #033170;
              "
            >
              {{
                clientJudgInfoTObject.judg_monthly +
                $t("DASHBOARD.FOR_COMPANY_JUDGEMENT")
              }}
            </div>
          </template>
        </div>
      </b-col>
      <b-modal
        id="modal-tall"
        centered
        variant="secondary"
        modal-class="py-0"
        body-class="py-0"
        size="md"
        footer-class="border-0"
        header-class="border-0 bg-white"
      >
        <template v-slot:modal-header="{ cancel }">
          <div class="w-100 d-flex justify-content-end">
            <button
              @click="cancel()"
              class="cross-close-btn border-dark text-dark font-weight-bold text-sm m-0 bg-white mr-2"
            >
              X
            </button>
          </div>
        </template>
        <b-card>
          <div class="my-0 py-0 px-2">
            <ul class="list-unstyled my-0 py-0">
              <li
                v-for="(OVERALL_JUDGEMENT, index) in $t(
                  'DASHBOARD.OVERALL_JUDGEMENTS'
                )"
                :key="index"
              >
                <span class="font-weight-bold block">
                  {{ OVERALL_JUDGEMENT.LABEL }}
                </span>
                <p class="mb-0">{{ OVERALL_JUDGEMENT.VALUE }}</p>
              </li>
            </ul>
          </div>
        </b-card>
        <template v-slot:modal-footer="{ cancel }">
          <div class="w-100">
            <button
              @click="cancel()"
              class="btn-sm float-left btn-custom-color px-1 ml-2"
            >
              {{ $t("CLOSE") }}
            </button>
          </div>
        </template>
      </b-modal>
    </b-row>

    <b-row class="d-sm-flex d-md-flex d-lg-none justify-content-center mb-3">
      <b-col cols="12" sm="12" class="text-center">
        <b-button
          @click="getThreeMonthsDetails"
          :class="{ mntbtn: mntBtnFlag, mnt2btn: !mntBtnFlag }"
          >3ケ月</b-button
        >
        <b-button
          @click="getSixMonthsDetails"
          :class="{ mntbtn: mntBtnFlag2, mnt2btn: !mntBtnFlag2 }"
          >6ケ月</b-button
        >
        <b-button
          @click="getAllMonthsDetails"
          :class="{ mntbtn: mntBtnFlag3, mnt2btn: !mntBtnFlag3 }"
          >12ケ月</b-button
        >
      </b-col>
    </b-row>

    <b-row>
      <b-col cols="11">
        <div class="d-flex justify-content-end">
          <div class="graph-color-legends mr-1"></div>
          <span class="label-color"> {{ $t("AVERAGEOFTHESAMESCALE") }}</span>
        </div>
      </b-col></b-row
    >
    <b-row class="w-full">
      <b-col cols="12 mb-3">
        <ImprovementExpenseComp
          :dataset="dataset"
          :colors="colors2"
          :labels="labels2"
          :total-months="selectedOption"
          :start-year="startYear"
          :start-month="startMonth"
          :optionlist="optionList"
          :second-graph="secondGraph"
          :key="redraw"
          expense-type="judgement"
        />
      </b-col>
    </b-row>

    <b-row class="d-flex justify-content-between mb-3">
      <b-col cols="12" class="d-flex justify-content-center">
        <div
          class="py-2 px-2 pillButtons bg-white d-flex justify-content-center align-items-center"
        >
          <b-link
            @click="goPrevMonth"
            href="javascript:;"
            class="link d-flex justify-content-center align-items-center"
          >
            <span class="">
              <i
                class="fa fa-caret-left fa-2x text-muted mr-2"
                aria-hidden="true"
              ></i>
            </span>
            <span class="mr-2 dark-blue">{{ $t("TOPREVIOUSMONTH") }}</span>
          </b-link>
          &nbsp;|
          <b-link
            @click="goNextMonth"
            href="javascript:;"
            class="link ml-2 d-flex justify-content-center align-items-center"
          >
            <span class="dark-blue">{{ $t("TONEXTMONTH") }}</span>
            <span class="">
              <i
                class="fa fa-caret-right fa-2x text-muted ml-2"
                aria-hidden="true"
              ></i>
            </span>
          </b-link>
        </div>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import ImprovementExpenseComp from "@/components/graphs/ImprovementExpenseJudgCompNew.vue";
import Monogram from "@/components/graphs/Monogram.vue";
import api from "@/shared/api";
import { mapGetters } from "vuex";
import { endPoint } from "@/shared/endPoint";

export default {
  components: { ImprovementExpenseComp, Monogram },
  data() {
    return {
      showFirst: true,
      showSecond: true,
      clientJudgInfoTObject: {},
      startYear: "",
      startMonth: "",
      mntBtnFlag: false,
      mntBtnFlag2: true,
      mntBtnFlag3: false,
      secondGraph: true,
      redraw: 0,
      expenseType: "judgement",
      selectedOption: 6,
      treeOptions: {
        checkbox: true,
      },
      optionList: [
        { key: "12か月", value: "12" },
        { key: "6か月", value: "6" },
        { key: "3か月", value: "3" },
      ],
      optionList2: ["valueA", "valueB"],
      colors: ["#04aeec", "#64e6a4", "#f5f886", "#ff9393", "#bfbfbf"],
      colors2: [
        "#ffd966",
        "#00b0f0",
        "#a9d08e",
        "#c65911",
        "#ff5353",
        "#a6a6a6",
      ],
      labels: [
        this.$t("OVERALLJUDGEMENTCHART.SLIM"),
        this.$t("OVERALLJUDGEMENTCHART.HEALTHY"),
        this.$t("OVERALLJUDGEMENTCHART.FAT"),
        this.$t("OVERALLJUDGEMENTCHART.METABO"),
        this.$t("OVERALLJUDGEMENTCHART.SPOILER"),
      ],
      labels2: [
        this.$t("RANKEXCELLENCE.EXCELLENCE"),
        this.$t("RANKEXCELLENCE.GREATCOMPANY"),
        this.$t("RANKEXCELLENCE.GOODCOMPANY"),
        this.$t("RANKEXCELLENCE.STANDARD"),
        this.$t("RANKEXCELLENCE.OVERWEIGHT"),
        this.$t("RANKEXCELLENCE.METABOLIC"),
      ],
      dataset: [],
      dataset2: [],
    };
  },
  computed: {
    ...mapGetters({
      userAccountData: "getUserDetails",
    }),
  },
  mounted() {
    this.getJudgementData();
  },
  methods: {
    getRank(judgeVal) {
      if (judgeVal) {
        let rankLetters = [
          {
            rank: "S",
            color1: "#FF7D66",
            color2: "#FF671F",
            color3: "#FF7D66",
          },
          {
            rank: "A",
            color1: "#A2DBFF",
            color2: "#4068EDE6",
            color3: "#A2DBFF",
          },
          {
            rank: "B",
            color1: "#C4EAFF",
            color2: "#93B5C4 ",
            color3: "#C4EAFF",
          },
          {
            rank: "C",
            color1: "#FFF040",
            color2: "#FA9E22",
            color3: "#FFF040",
          },
          {
            rank: "D",
            color1: "#85F780",
            color2: "#0AF932",
            color3: "#85F780",
          },
          {
            rank: "E",
            color1: "#EDBEDF",
            color2: "#F90AF5",
            color3: "#EDBEDF",
          },
        ];
        let yName;
        yName = rankLetters[parseInt(judgeVal) - 1];
        return yName.rank;
      } else {
        return false;
      }
    },
    getThreeMonthsDetails() {
      this.selectedOption = 3;
      (this.mntBtnFlag = true),
        (this.mntBtnFlag2 = false),
        (this.mntBtnFlag3 = false),
        this.getJudgementData(this.selectedOption);
    },
    getSixMonthsDetails() {
      this.selectedOption = 6;
      (this.mntBtnFlag = false),
        (this.mntBtnFlag2 = true),
        (this.mntBtnFlag3 = false),
        this.getJudgementData(this.selectedOption);
    },
    getAllMonthsDetails() {
      this.selectedOption = 12;
      (this.mntBtnFlag = false),
        (this.mntBtnFlag2 = false),
        (this.mntBtnFlag3 = true),
        this.getJudgementData(this.selectedOption);
    },
    formatJudgeMonthly() {
      if (!this.clientJudgInfoTObject.hasOwnProperty("judg_monthly")) {
        if (new Date().getMonth() < 10) {
          let dateString =
            new Date().getFullYear() +
            this.$t("YEARJP") +
            "0" +
            (new Date().getMonth() + 1) +
            this.$t("MONTHJP");
          return dateString;
        } else {
          let dateString =
            new Date().getFullYear() +
            this.$t("YEARJP") +
            (new Date().getMonth() + 1) +
            this.$t("MONTHJP");
          return dateString;
        }
      } else {
        let dateString =
          String(this.clientJudgInfoTObject.judg_monthly).slice(0, 4) +
          this.$t("YEARJP") +
          String(this.clientJudgInfoTObject.judg_monthly).slice(4, 6) +
          this.$t("MONTHJP");
        return dateString;
      }
    },
    async getJudgementData(dropDown = false) {
      if (dropDown) {
        this.startYear = "";
        this.startMonth = "";
      }
      let { data } = await api.post(
        endPoint.EVALUATIONCOMPANYJUDGEMENTCOMPARISON,
        {
          client_no: this.userAccountData.client_account_no,
          year: this.startYear,
          total_month: this.selectedOption,
          start_month: this.startMonth,
        }
      );

      if (data.success) {
        let {
          clientJudgInfoTObject,
          clientJudgeCompanyAverageListObject,
          clientJudgInfoTListObject,
          year,
          start_month,
        } = data.data;

        this.clientJudgInfoTObject = clientJudgInfoTObject;
        this.clientJudgInfoTObject.judg_monthly = this.formatJudgeMonthly();
        clientJudgeCompanyAverageListObject.name = "同規模平均";
        clientJudgInfoTListObject.name = "貴社判定";

        this.dataset = [
          clientJudgInfoTListObject,
          clientJudgeCompanyAverageListObject,
        ];
        this.startYear = parseInt(year);
        this.startMonth = parseInt(start_month);
        this.redraw = this.redraw + 1;
      } else {
        this.clientJudgInfoTObject.judg_monthly = this.formatJudgeMonthly();
        this.dataset = [
          {
            expenseItem: "",
            expense_item_cd: "",
            performance: [],
          },
        ];
        this.redraw = this.redraw + 1;
      }
    },
    goPrevMonth() {
      if (this.startMonth == 1) {
        this.startYear = this.startYear - 1;
        this.startMonth = 12;
        this.getJudgementData();
      } else {
        if (this.startMonth <= 12 && this.startMonth > 1) {
          this.startMonth -= 1;
          this.getJudgementData();
        }
      }
    },
    goNextMonth() {
      if (this.startMonth >= 12) {
        this.startYear = this.startYear + 1;
        this.startMonth = 1;
        this.getJudgementData();
      } else {
        if (this.startMonth >= 1 && this.startMonth < 12) {
          this.startMonth += 1;
          this.getJudgementData();
        }
      }
    },
    yealyExpense() {
      this.$router.push({ name: "improvement_expense" });
    },
    monthlyExpense() {
      this.$router.push({ name: "improvement_expense_judgement" });
    },
  },
};
</script>

<style scoped lang="scss">
$primary: #ac000c !default;
$cell_color: #ffffff;
$border_color: #221e16;
$time_font: 8px;

.label-color {
  color: #bccadd;
  font-size: 11px !important;
}
@media only screen and (min-width: 991px) {
  .label-color {
    margin-right: 12px !important;
  }
}
.graph-color-legends {
  width: 12px !important;
  height: 12px !important;
  margin-top: 2px !important;
  border-radius: 50%;
  background-color: #bccadd;
}
.list-circle a {
  list-style-type: circle;
}
.dark-blue {
  color: #033170;
}
.pillButtons {
  box-shadow: 0px 0px 30px #0000001a;
  border-radius: 16px;
  width: 10rem;
  height: 31px !important;
  font-weight: 500 !important;
}
.cal-hr {
  font-size: 12px;
  font-family: sans-serif;
  margin: 0;
  padding: 10px;
}
.no-margin {
  margin-left: 0px;
  margin-right: 0px;
}

.cal-hr hr {
  border: none;
  border-top: 1px solid rgba(0, 0, 0, 0.2);
  margin: 0.6em 2em;
}

.cal-hr a {
  position: fixed;
  top: 5px;
  right: 5px;
  color: white;
}

.custom_month_event {
  margin-left: -6px;
  font-size: 10px;
}

.card-font {
  font-size: $time_font;
}
.vuecal--week-view .vuecal_custom_event * {
  word-break: break-word;
  padding: 0%;
}

ul,
ol {
  padding: 0;
}
</style>
<style scoped lang="scss">
@media only screen and (min-width: 991px) {
  .border-radius-imprv {
    border-top-left-radius: 0px;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    border-bottom-left-radius: 0px;
  }

  .mnt2border {
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 0px;
  }
  .mntborder {
    border-top-left-radius: 4px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 4px;
  }
}
.course-btn {
  color: #ffffff !important;
  border: none !important;
  padding: 0rem 0.7rem 0rem 0.7rem !important;
}

.btn-custom-color {
  background-color: #062854;
  border-color: #062854;
  color: white;
}

.list-legend {
  font-size: 12px;
}

.mntbtn {
  padding: 5px 15px 5px 15px !important;
  min-width: 50px !important;
  font-size: 12px;
  cursor: default !important;
  box-shadow: 0px 0px 50px #0000000f !important;
  background: transparent linear-gradient(196deg, #0e4a98 0%, #003170 100%) 0%
    0% no-repeat padding-box;
  border-color: #ffffff !important;
}
.mnt2btn {
  padding: 5px 15px 5px 15px !important;
  min-width: 50px !important;
  font-size: 12px;
  cursor: default !important;
  box-shadow: 0px 0px 50px #0000000f !important;
  background-image: none !important;
  color: gray !important;
  background-color: white !important;
  background: white linear-gradient(196deg, #0e4a98 0%, #003170 100%) 0% 0%
    no-repeat padding-box;
  border-color: #ffffff !important;
}
.rankIcon {
  margin-top: -31px;
}
@media only screen and (max-width: 500px) {
  .rankIcon {
    margin-top: unset;
  }
}
@media only screen and (max-width: 991px) {
  .judgCompMobile {
    margin-top: 1.5rem !important;
  }
}
</style>
